.msg_container_base {
	background: #e5e5e5;
	margin: 0;
	padding: 0 10px 30px;
}

.msg_container {
	padding: 10px;
	overflow: hidden;
	display: flex;
}

.base_receive > .avatar:after {
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	width: 0;
	height: 0;
	border: 5px solid #fff;
	border-left-color: rgba(0, 0, 0, 0);
	border-bottom-color: rgba(0, 0, 0, 0);
}

.base_sent {
	justify-content: flex-end;
	align-items: flex-end;
}
.base_sent > .avatar:after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 0;
	height: 0;
	border: 5px solid white;
	border-right-color: transparent;
	border-top-color: transparent;
	box-shadow: 1px 1px 2px rgba(black, 0.2);
}

.msg_receive {
	padding-left: 0;
	margin-left: 0;
}
.msg_sent {
	padding-bottom: 20px !important;
	margin-right: 0;
}
.messages {
	background: white;
	padding: 10px;
	border-radius: 2px;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
	max-width: 100%;
}
.messages > p {
	font-size: 13px;
	margin: 0 0 0.2rem 0;
}
.messages > time {
	font-size: 11px;
	color: #ccc;
}

.base_sent .messages {
	background: #2dadb0;
	border-radius: 8px 8px 0px 8px;
	-webkit-border-radius: 8px 8px 0px 8px;
	color: #fff;
	border-bottom: 1px solid #108bb4;
}
.base_sent::after {
	top: 0px;
	width: 0;
	height: 0;
	border-top: 10px solid transparent;
	border-left: 10px solid #2dadb0;
	border-bottom: 0px solid transparent;
}
.base_receive .messages {
	background: #f3f5f9;
	border-radius: 0 8px 8px 8px;
	border-bottom: 1px solid #ccc;
}
.base_receive::before {
	width: 0;
	height: 0;
	border-top: 0px solid transparent;
	border-right: 10px solid #f3f5f9;
	border-bottom: 10px solid transparent;
}

.messages-container {
	clear: both;
}

.bd-callout-warning {
	border-left-color: rgb(240, 173, 78) !important;
}

.bd-callout {
	padding: 0.25rem;
	margin-bottom: 1.25rem;
	border: 1px solid rgb(238, 238, 238);
	border-left-width: .25rem;
	border-radius: .25rem;
}

.card {
    margin: 10px;
    border: 2px solid rgb(37, 42, 51);
}
.alert {
	position: relative;
	padding: 0.35rem 0.35rem;
	border: 1px solid rgba(0, 0, 0, 0);
	border-radius: .25rem;
	font-size: 13px;
	color: rgb(208, 48, 48);
	margin-bottom: 0px;
}

.chat-avatar {
	width: 36px;
	height: 36px;
	float: left;
	margin-right: 10px;
  }
  .chat-user-name {
	padding: 10px;
	background: rgb(121, 114, 121);
  }
  .chat-user, .chat-channel-item {
	padding: 8px 10px;
	border-bottom: 1px solid #e7eaec;
  }
  .chat-user a {
	color: inherit;
  }

  .chat-channel-name {
	padding: 10px;
	background: rgb(121, 114, 121);
  }
  .chat-channel, .chat-users{
	padding: 8px 10px;
  }
  .chat-channel a {
	color: inherit;
  }